import React from "react";
import "./card.styles.scss";

const Card = ({ monster }) => {
  return (
    <div
      className={`${
        monster.id % 2 === 0 ? "card-container-success" : "card-container"
      } `}>
      <img
        alt={`${monster}`}
        src={`https://robohash.org/${monster.id}?set=set2&size=180x180`}
      />
      <h1>{monster.name}</h1>
      <p>{monster.email}</p>
    </div>
  );
};

export default Card;
